import Vue from 'vue'
import VueRouter from 'vue-router'
import Html1 from '../views/Files/Html/html1.vue'
import Html2 from '../views/Files/Html/html2.vue'
import Css1 from '../views/Files/Css/css1.vue'
import Css2 from '../views/Files/Css/css2.vue'
import Start from '../views/Files/start.vue'
import jsOrigin from '../views/Files/Js/origin.vue'
import js1 from '../views/Files/Js/Js1.vue'
import dev from '@/views/Home.vue'
Vue.use(VueRouter)

const routes = [
  {
    path:'/',
    name:'Login',
    component: () => import('../views/Login.vue'),
  },
  {
  path: '/home',
  name: 'Home',
  component: () => import('../views/Blog.vue'),
  children: [{
      path: '/home/html1',
      name: 'htm1',
      component: Html1
    },
    {
      path: '/home/html2',
      name: 'htm1',
      component: Html2
    },
    {
      path: '/home/css1',
      name: 'css1',
      component: Css1
    },
    {
      path: '/home/css2',
      name: 'css2',
      component: Css2
    },
    {
      path:'/home/start',
      name:'Start',
      component:Start
    },
    {
      path:'/home/jsOrigin',
      name:'jsOrigin',
      component:jsOrigin
    },
    {
      path:'/home/js1',
      name:'js1',
      component:js1
    }
    
  ]
}, 
{
  path:'/dev',
  name:'deving',
  component:dev
}

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router