<template>
  <div class="contain">
    <div class="boxes" v-if="nowpage === 1" >
      <item v-for='item in itemList' :key='item.id' :msg='item'></item>
    </div>

    <div class="boxes" v-if='nowpage === 2'>
      <item></item>
      <item></item>
      <item></item>

      <item></item>
      <item></item>
      <item></item>

      <item></item>

    </div>

    <div class="block">
      <bar></bar>
    </div>
  </div>
  
</template>

<script>
import item from './com/item.vue'
import bar from './com/bar.vue'
import {useStore} from '@/store/index.js'
export default {
  setup(){
    const store = useStore()
    return {
      store
    }
  },
  components:{
    item,
    bar
  },
  computed:{
    total(){
      let total = document.querySelectorAll('.box')
      return total
    },
    nowpage(){
      return this.store.nowposition
    }
  },
  data() {
    return {
      itemList:[
        {name:'css',id:1,url:require('@/assets/imgs/logo/css/css.jpg'),show:'css知识点'},
        {name:'html',id:2,url:require('@/assets/imgs/logo/html/html1.jpg'),show:'html知识点'},
        {name:'js',id:3,url:require('@/assets/imgs/logo/js/js1.jpg'),show:'js'},
        {name:'vue',id:4,url:require('@/assets/imgs/logo/vue/vue.jpg'),show:'vue知识点'},
      ]
    }
  },  
}


</script>

<style lang='less' scoped>
.contain{
  .boxes{  
    position: relative;
    flex-wrap: wrap;
    justify-content: space-around;
    overflow: hidden; 
    display: flex;
    margin-top: 20px;
    min-height: 130vh;
    width: 100%;    
  }
}

.block{

  // background-color: red;
  margin-bottom: 40px;
}

</style>