<template>
  <div class="bar">
  <el-pagination
    :current-page='1'
    :page-size="1"
    :pager-count="11"
    layout="prev, pager, next"
    @current-change='change'
    :total="1">
  </el-pagination>
  </div>
</template>

<script>
import {useStore} from '@/store/index.js'
export default {
  setup(){
    const store = useStore()
    return {
      store
    }
  },
  data() {
    return {
      // nowpage:1
    }
  },
  methods: {
    change(val){
      this.store.$patch(state=>{
        state.nowposition = val
      })
    }
  },
}
</script>

<style lang='less' scoped> 
 .bar{
   /* position: relative; */
   max-height: 5vh;
  //  background-color: red;
   text-align: center;
 }
</style>