import Vue from 'vue'
import App from './App.vue'
import router from './router'
import {container,aside,header,main,footer,pagination} from 'element-ui'; //主页
import {Menu,Submenu,Icon,MenuItem,MenuItemGroup,RadioGroup,RadioButton,Progress,Image} from 'element-ui';//侧边栏
import 'element-ui/lib/theme-chalk/index.css';
import { createPinia,PiniaVuePlugin  } from 'pinia'
Vue.use(PiniaVuePlugin)

Vue.config.productionTip = false
// 主页
Vue.use(footer);
Vue.use(container);
Vue.use(aside);
Vue.use(header);
Vue.use(main);
// 侧边栏
Vue.use(Menu)
Vue.use(pagination)
Vue.use(Submenu)
Vue.use(Icon)
Vue.use(MenuItem)
Vue.use(MenuItemGroup)
Vue.use(RadioGroup)
Vue.use(RadioButton)
Vue.use(Progress)
Vue.use(Image)
//md
import 'github-markdown-css'
import 'highlight.js/styles/github.css'

const pinia =createPinia()

new Vue({
  router,
  pinia,
  render: h => h(App)
}).$mount('#app')
