<template>
 <div class="top" v-show="isShow">
  <h3 @click="backTop">一柒的blog
  </h3>
  
 </div>
</template>

<script>
// import {useStore} from '../store/index.js'

export default {
  // setup() {
  //   const store = useStore()
  //   return{
  //     store,
  //   }
  // },
  data() {
    return {
      isShow:true,
      scroll:''
    }
  },
  methods: {
    scrollTop(){
      this.scroll = document.documentElement.scrollTop
    },
    backTop(){
      document.documentElement.scrollTop = document.body.scrollTop =0
    },
    // up(){
    //   this.store.$patch(state=>{
    //     state.nowposition++
    //   })
    // }
  },
  mounted() {
    window.addEventListener('scroll', this.scrollTop)
  },
  watch:{
    scroll(now,old){
      if(now-old>0){
        this.isShow = false
      }else{
        this.isShow=true
      }
    }
  }
}
</script>

<style lang='less'>
  .top{
    position: fixed;
    top: 0;
    height: 5vh;
    width: 100vw;
    background: rgba(255, 255, 255,0.7);
    line-height: 1vh;
    h3{
      font-weight: 800;
      font-family:Georgia, 'Times New Roman', Times, serif;
      margin-left: 3vw;
      color:#4c4948;
      &:hover{
        color:#49b1f5;
      }
    }

  }
</style>